@import url(https://use.fontawesome.com/releases/v5.7.1/css/all.css);
body {
  margin: 0;
  padding: 0;
  font: 16px 'Ubuntu', sans-serif;
  background: #ecf0f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.tasker ul, .tasker li,
.tasker .error,
.tasker button,
.tasker input {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  background: transparent;
}

.tasker-header button, .tasker-body .task button {
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
}

/*PEN STYLES*/
.tasker {
  max-width: 500px;
  margin: 20px auto;
}

.tasker ul .empty{
  padding: 20px 15px;
  margin: 0;
  min-height: 200px;
}
.tasker ul {
  background: #fff;
}
.tasker li,
.tasker .error,
.tasker button,
.tasker input {
  font: 18px/1.25em Helvetica, Arial, Sans-serif;
}

.tasker-header,form {
  display: -webkit-inline-flex;
  display: inline-flex;
  background: #313e50;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
}
.tasker-header input,
.tasker-header button {
  color: #fff;
  box-sizing: border-box;
  font-size: 1.25em;
  padding: 14px;
}
.tasker-header input {
  -webkit-flex-grow: 2;
          flex-grow: 2;
  width: 100%;
}
.tasker-header button {
  background: #9c27b0;
  border-left: 1px solid #9c27b0;
  cursor: pointer;
}
.tasker-header button:hover {
  background: #8d0770;
}
.tasker .tasker-body .error {
  display: block;
  background: rgba(237, 28, 36, 0.7);
  color: #fff;
  padding: 14px;
  margin-bottom: 10px;
  border-radius: 5px;
  text-align: center;
}
.tasker-body .task {
  display: block;
  position: relative;
  padding: 14px 40px 14px 14px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  color: #313e50;
  text-align: left;
  -webkit-transition: all .4s linear;
  transition: all .4s linear;
}
.tasker-body .task:hover{
	background-color: #8284a3;
	color:#ddd;
}
.tasker-body .task:last-child {
  border-bottom: none;
}
.tasker-body .task:hover > button {
  opacity: 1;
}
.tasker-body .task.completed {
  color: #cdcdcd;
  text-decoration: line-through;
}
.tasker-body .task input {
  margin-right: 10px;
}
.tasker-body .task button {
  color: #cdcdcd;
  margin: 14px;
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
  cursor: pointer;
}
.tasker-body .task button:hover {
  color: #ed1c24;
}

.App {
  text-align: center;
}
.App-header {
  min-height: 100vh;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
  position: relative;
  padding-top: 170px;
  width: auto;
  border-top-left-radius: 22px;
  border-top-right-radius: 22px;
  background: -webkit-linear-gradient(315deg, rgba(158, 15, 184, 0.7) -10%, rgba(5, 232, 255, 0.8) 180%), url(https://static.vecteezy.com/system/resources/previews/000/101/253/non_2x/vector-free-abstract-background-1.jpg);
  background: linear-gradient(135deg, rgba(158, 15, 184, 0.7) -10%, rgba(5, 232, 255, 0.8) 180%), url(https://static.vecteezy.com/system/resources/previews/000/101/253/non_2x/vector-free-abstract-background-1.jpg);
  background-size: cover;
  
}

.App-header h2,
.App-header h4 {
  font-size: 64px;
  font-weight: 300;
  color: #dfdfdf;
  margin: 10px 0;
}
.App-header h4 {
  font-size: 20px;
  margin: 0 0 50px 0;
}
.App-header h2 span,
.App-header h4 span {
  font-weight: 700;
}
.App-header h1 {
  margin: 0;
  padding: 30px 10px;
  min-height: 100px;
  color: #d3d3d3;
}

.App-link {
  color: #dfdfdf;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
  text-decoration: none;
  font-size: 20px;
  display: block;
  -webkit-transform: scale(.9);
          transform: scale(.9);
}
.App-link:hover {color: #ffffff;-webkit-transform: scale(1);transform: scale(1)}


