.App {
  text-align: center;
}
.App-header {
  min-height: 100vh;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
  position: relative;
  padding-top: 170px;
  width: auto;
  border-top-left-radius: 22px;
  border-top-right-radius: 22px;
  background: linear-gradient(135deg, rgba(158, 15, 184, 0.7) -10%, rgba(5, 232, 255, 0.8) 180%), url(https://static.vecteezy.com/system/resources/previews/000/101/253/non_2x/vector-free-abstract-background-1.jpg);
  background-size: cover;
  
}

.App-header h2,
.App-header h4 {
  font-size: 64px;
  font-weight: 300;
  color: #dfdfdf;
  margin: 10px 0;
}
.App-header h4 {
  font-size: 20px;
  margin: 0 0 50px 0;
}
.App-header h2 span,
.App-header h4 span {
  font-weight: 700;
}
.App-header h1 {
  margin: 0;
  padding: 30px 10px;
  min-height: 100px;
  color: #d3d3d3;
}

.App-link {
  color: #dfdfdf;
  transition: all .4s ease;
  text-decoration: none;
  font-size: 20px;
  display: block;
  transform: scale(.9);
}
.App-link:hover {color: #ffffff;transform: scale(1)}
